import React, {useEffect, useState} from "react";
import ReactPlayer from "react-player";
import SpotifyWebApi from "spotify-web-api-node";

function App() {
    const clientId = "4db9890d0d0e4334833cf1cfdf73e9de";
    const redirectUri = "https://sserjik8.github.io/nukeradio/";
    const scope = "user-read-private user-read-email";

    function handleLogin() {
        const authorizeUrl = `https://accounts.spotify.com/authorize?client_id=${clientId}&response_type=token&redirect_uri=${encodeURIComponent(
            redirectUri
        )}&scope=${encodeURIComponent(scope)}`;
        window.location.href = authorizeUrl;
    }

    const urlParams = new URLSearchParams(window.location.hash.substr(1));
    const accessToken = urlParams.get("access_token");
    const spotifyApi = new SpotifyWebApi();
    spotifyApi.setAccessToken(accessToken);

    const [userData, setUserData] = useState([]);
    const [userPlaylist, setUserPlaylist] = useState([]);
    useEffect(() => {
        spotifyApi
            .getMe()
            .then(function (data) {
                console.log("Some information about the authenticated user", data.body);
                setUserData(data.body);
            })
            .catch(function (err) {
                console.log("Something went wrong!", err);
            });
        spotifyApi
            .getUserPlaylists(userData.display_name)
            .then(function (data) {
                console.log("Retrieved playlists", data.body.items);
                setUserPlaylist(data.body.items);
            })
            .catch(function (err) {
                console.log("Something went wrong!", err);
            });
    }, [userData.display_name]);

    useEffect(() => {
        spotifyApi
            .getRecommendations({
                min_energy: 0.4,
                seed_artists: ["6mfK6Q2tzLMEchAr0e9Uzu", "4DYFVNKZ1uixa6SQTvzQwJ"],
                min_popularity: 50,
            })
            .then(
                function (data) {
                    let recommendations = data.body;
                    console.log(recommendations);
                },
                function (err) {
                    console.log("Something went wrong!", err);
                }
            );
    }, [userData.display_name]);

    const [currentTrackIndex, setCurrentTrackIndex] = useState(0);
    const [isPlaying, setIsPlaying] = useState(false);

    function handlePause() {
        setIsPlaying(false);
    }

    function handlePlay() {
        setIsPlaying(true);
    }

    useEffect(() => {
        function adjustFooterPosition() {
            const wrapper = document.querySelector("main");
            const footer = document.querySelector("footer");
            const wrapperHeight = wrapper.offsetHeight;
            const footerHeight = footer.offsetHeight;
            const windowHeight = window.innerHeight;

            if (wrapperHeight + footerHeight < windowHeight) {
                footer.style.position = "fixed";
                footer.style.bottom = "0";
            } else {
                footer.style.position = "static";
            }
        }

        function handleResize() {
            adjustFooterPosition();
        }

        window.addEventListener("resize", handleResize);
        adjustFooterPosition();

        return () => {
            window.removeEventListener("resize", handleResize);
        };
    }, []);

    const [selectedPlaylistId, setSelectedPlaylistId] = useState(null);

    function handleDataPlaylist(playlistId) {
        setSelectedPlaylistId(playlistId);
    }

    const [playlistTracks, setPlaylistTracks] = useState([]);
    const [selectedTrack, setSelectedTrack] = useState(null);

    useEffect(() => {
        if (selectedPlaylistId) {
            spotifyApi
                .getPlaylistTracks(selectedPlaylistId)
                .then(function (data) {
                    console.log("Retrieved playlist tracks", data.body.items);
                    const tracks = data.body.items.map((item) => ({
                        id: item.track.id,
                        name: item.track.name,
                        artists: item.track.artists[0].name,
                        previewUrl: item.track.preview_url,
                        imageUrl: item.track.album.images[0].url,
                    }));
                    setPlaylistTracks(tracks);
                    setCurrentTrackIndex(0); // Reset the current track index to the first track
                })
                .catch(function (error) {
                    console.log("Error retrieving playlist tracks", error);
                });
        }
    }, [selectedPlaylistId]);

    const [searchPlaylist, setSearchPlaylist] = useState("");
    const [searchedTracks, setSearchedTracks] = useState([]);

    function handleSearchTracks(e) {
        setSearchPlaylist(e.target.value);
    }

    function playNextTrack() {
        if (currentTrackIndex < playlistTracks.length - 1) {
            setCurrentTrackIndex(currentTrackIndex + 1);
            setIsPlaying(true);
        }
    }

    function playPreviousTrack() {
        if (currentTrackIndex > 0) {
            setCurrentTrackIndex(currentTrackIndex - 1);
            setIsPlaying(true);
        }
    }

    function handlePause() {
        setIsPlaying(false);
    }

    function handlePlay() {
        setIsPlaying(true);
    }

    return (
        <div>
            <header>
                <h1>NUKE RADIO</h1>
                <input
                    placeholder=" "
                    type="text"
                    value={searchPlaylist}
                    onChange={handleSearchTracks}
                />
                <div>
                    {!accessToken && (
                        <button onClick={handleLogin}>  Spotify</button>
                    )}
                    {accessToken && <p>{userData.display_name}</p>}
                </div>
            </header>
            <nav>
                <ul>
                    <li>PLAYLIST</li>
                </ul>
            </nav>
            <main>
                <article>
                    {userPlaylist &&
                        userPlaylist
                            .filter((playlist) =>
                                playlist.name.toLowerCase().includes(searchPlaylist.toLowerCase())
                            )
                            .map((playlist) => (
                                <div
                                    onClick={() => handleDataPlaylist(playlist.id)}
                                    key={playlist.id}
                                >
                                    <p>{playlist.name}</p>
                                    {playlist.images[0] && (
                                        <img src={playlist.images[0].url} alt={playlist.name}/>
                                    )}
                                </div>
                            ))}
                </article>
                <aside>
                    {searchedTracks &&
                        searchedTracks
                            .filter((track) =>
                                track.name.toLowerCase().includes(searchPlaylist.toLowerCase())
                            )
                            .map((track) => (
                                <div key={track.id} onClick={() => setSelectedTrack(track)}>
                                    <p>{track.name}</p>
                                    <img src={track.album.images[0].url} alt={track.name}/>
                                </div>
                            ))}
                    {playlistTracks &&
                        playlistTracks.map((track, index) => (
                            <div key={track.id} onClick={() => setCurrentTrackIndex(index)}>
                                <p>
                                    {track.name} | {track.artists}
                                </p>
                                {console.log(track.imageUrl)}
                                <img src={track.imageUrl} alt={track.name}/>
                            </div>
                        ))}
                </aside>
            </main>
            <footer>
                {playlistTracks.length > 0 && (
                    <div className="player">
                        <div>
                            <p>{playlistTracks[currentTrackIndex].name} </p>
                            <p>{playlistTracks[currentTrackIndex].artists}</p>
                            <img
                                src={playlistTracks[currentTrackIndex].imageUrl}
                                alt={playlistTracks[currentTrackIndex].name}
                            />
                            {playlistTracks[currentTrackIndex].previewUrl ? (
                                <div>
                                    <div className="btn_player"><img
                                        src="https://www.svgrepo.com/show/424368/music-music-player-player-2.svg"
                                        alt="Previous Track"
                                        onClick={playPreviousTrack}
                                    ></img></div>
                                    <ReactPlayer
                                        url={playlistTracks[currentTrackIndex].previewUrl}
                                        playing={isPlaying}
                                        controls
                                        width="100%"
                                        height="50px"
                                    />
                                    <div className="btn_player">
                                        <img
                                            src="https://www.svgrepo.com/show/424366/music-next-next-button.svg"
                                            alt="Next Track"
                                            onClick={playNextTrack}
                                        ></img>
                                    </div>
                                </div>
                            ) : (
                                <p>No preview available</p>
                            )}
                        </div>
                    </div>
                )}
            </footer>
        </div>
    );
}

export default App;
